.book-list-container {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px 100px;
  @media screen and (min-width: 720px) {
    body {
      padding: 10px;
    }
  }  
}

.book-table {
  width: 60%;
  border-collapse: collapse;
}
.book-table th,
.book-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}
.book-table th {
  background-color: #F2F2F2;
}
.book-table tr:hover {
  background-color: #F5F5F5;
}
.book-table button {
  padding: 5px 8px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  width: fit-content;
  margin: 0 auto;
}
.book-table button:hover {
  background-color: #45A049;
}
